.categories-container {
	

	ul {
		list-style: none;
		display: flex;
		flex-wrap: wrap;
		gap: 15px;
		padding: 0;

		li {
			border-radius: 30px;

			&:hover {
				filter: brightness(0.9);
			}

			a {
				text-decoration: none;
				text-transform: capitalize;
				height: 100%;
				width: 100%;
				padding: 5px 15px;
				font-weight: 500;
				color: white;
			}
		}
	}
}
